import { Component, OnInit } from '@angular/core';
import { NavParams, ModalController } from '@ionic/angular';
import { servicio } from '../../models/mensaje';
import { DatosService } from '../../servicios/datos.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-formulario',
  templateUrl: './formulario.component.html',
  styleUrls: ['./formulario.component.scss'],
})
export class FormularioComponent implements OnInit {

  id :any;
  servicio:any;
  precio:number;
  nombre:string;
  direccion:string;

  constructor(private navparams :NavParams, private modal : ModalController, private dataService: DatosService, private router:Router) { }

  ngOnInit() {
    this.id =  this.navparams.get('id');
    this.servicio = this.navparams.get('servicio');
    this.precio = this.navparams.get('precio');
  }

  closeModal(){
    this.modal.dismiss();
  }

  confirmarPedido(){
    const servicio:servicio = {
      direccion: this.direccion,
      nombre: this.nombre,
      servicio: this.servicio,
      precio: this.precio
    }
    this.dataService.sendServicio(servicio, this.id);
    this.router.navigate(['/servicios',this.id]);
    this.modal.dismiss();
  }

}
