import { Component, OnInit } from '@angular/core';
import { NavParams, ModalController } from '@ionic/angular';
import { mensaje } from '../../models/mensaje';
import { DatosService } from '../../servicios/datos.service';

@Component({
  selector: 'app-dato',
  templateUrl: './dato.component.html',
  styleUrls: ['./dato.component.scss'],
})
export class DatoComponent implements OnInit {

  public mensajes = [];
  public message:mensaje;
  public datos :any;
  public room : any;
  public msg:string;

  constructor(private navparams :NavParams, private modal : ModalController, private datoService:DatosService) { }

  ngOnInit() {
    this.datos = this.navparams.get('dato');
    this.datoService.getDato(this.datos.id).subscribe(room => {
      this.room = room;
    })
  }

  closeModal(){
    this.modal.dismiss();
  }

  sendMensaje(){
    const mensa:mensaje = {
      contenido: this.msg,
      tipo:'text',
      date:new Date()
    }
    this.datoService.sendmsg(mensa, this.datos.id)
    this.msg = '';
  }

}
