import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { map } from 'rxjs/operators';
import { mensaje, servicio } from '../models/mensaje';
import { firestore } from 'firebase';
import { HttpClient } from '@angular/common/http';
import { Componente } from '../interfaces/interfaces';


export interface dataFire {
  descripcion: string 
  titulo: string 
  id : string
  img : string
}

export interface userFire{
  name:string;
  uid:string;
}

@Injectable({
  providedIn: 'root'
})
export class DatosService {

  constructor(private db :AngularFirestore, private http:HttpClient) { }

  getDatos(){
    return this.db.collection('datos').snapshotChanges().pipe(map( dato => {
      return dato.map(a => {
        const data = a.payload.doc.data() as dataFire;
        data.id = a.payload.doc.id;
        return data;
      })
    }))
  }

  getDato(datoId:string){
    return this.db.collection('datos').doc(datoId).valueChanges()
  }

  getUser(datoId:string){
    return this.db.collection('users').doc(datoId).valueChanges()
  }

  getUsers(){
    return this.db.collection('users').snapshotChanges().pipe(map( dato => {
      return dato.map(a => {
        const data = a.payload.doc.data() as any;
        return data;
      })
    }))
  }


  sendmsg(mensaje: mensaje, datoId:string){
    this.db.collection('datos').doc(datoId).update({
      mensaje: firestore.FieldValue.arrayUnion(mensaje)
    })
  }

  //guardar servicio
  sendServicio(servicio: servicio, id:string){
    this.db.collection('users').doc(id).update({
      servicio: firestore.FieldValue.arrayUnion(servicio)
    })
  }
  

  getMenuOptions(){
    return this.http.get<Componente[]>('/assets/menu.json');
  }

}
